import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';

@Component({
  selector: 'app-main-community',
  templateUrl: './main-community.component.html',
  styleUrls: ['./main-community.component.css']
})
export class MainCommunityComponent implements OnInit {
private menuList: [{ title: string, path: string }];
  constructor(
    private route: ActivatedRoute,
    private router: Router
  ) {
    this.menuList = [
      { title: "High There", path: "highthere" },
      { title: "Weed Life", path: "weedlife" },
      { title: "Duby", path: "duby" }
      // { title: "Our Board", path:"board" }
    ]
  }

  ngOnInit() {
  }

}



