import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { MainCommunityComponent } from './main-community/main-community.component';
import { HighthereCommunityComponent } from './highthere-community/highthere-community.component';
import { WeedlifeCommunityComponent } from './weedlife-community/weedlife-community.component';
import { DubyCommunityComponent } from './duby-community/duby-community.component';
// import { StoreCommunityComponent } from './store-Community/store-Community.component';
// import { VoucherListCommunityComponent } from './store-Community/voucher-list-Community/voucher-list-Community.component';
// import { VoucherItemCommunityComponent } from './store-Community/voucher-item-Community/voucher-item-Community.component';

const CommunityRoutes: Routes = [
  { path: '', component: MainCommunityComponent, children: [
      {path: '', redirectTo: 'highthere', pathMatch: 'full'},
      {
        path: 'highthere', component: HighthereCommunityComponent
      },
      {
        path: 'weedlife', component: WeedlifeCommunityComponent
      },
      {
        path: 'duby', component: DubyCommunityComponent
      }
    ]
  }

];

@NgModule({
  imports: [RouterModule.forChild(CommunityRoutes)],
  exports: [RouterModule]
})
export class CommunityRoutingModule { }



