import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { AgmCoreModule } from '@agm/core';
import { NglModule } from 'ng-lightning/ng-lightning';

import { CommunityRoutingModule } from './community.routing';
import { MainCommunityComponent } from './main-community/main-community.component';
import { HighthereCommunityComponent } from './highthere-community/highthere-community.component';
import { WeedlifeCommunityComponent } from './weedlife-community/weedlife-community.component';
import { DubyCommunityComponent } from './duby-community/duby-community.component';

@NgModule({
  imports: [
    CommonModule,
    CommunityRoutingModule,
    AgmCoreModule,
    NglModule
  ],
  declarations: [MainCommunityComponent,
    HighthereCommunityComponent,
    WeedlifeCommunityComponent,
    DubyCommunityComponent]
})
export class CommunityModule { }



